import { Component, Inject, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { MatBottomSheetRef, MAT_BOTTOM_SHEET_DATA } from '@angular/material/bottom-sheet';
import { MatSnackBar } from '@angular/material/snack-bar';
import { DepartmentModel } from '@models/department.model';
import { HttpResponseModel } from '@models/http/response.model';
import { DepartmentService } from '@services/department.service';
import { ConfigService } from '@services/config.service';
import { AuthenticationService } from '@services/authentication.service';

@Component({
  selector: 'app-department-play',
  templateUrl: './department-play.component.html',
  styleUrls: ['./department-play.component.css']
})
export class DepartmentPlayComponent implements OnInit {
  sse = '';
  action = '';

  depfg: FormGroup = new FormGroup({
    name: new FormControl(
      '',
      [
        Validators.required,
        Validators.pattern(/^.{1,75}$/),
      ],
    ),
  });

  delfg: FormGroup = new FormGroup({
  });

  constructor(
    @Inject(MAT_BOTTOM_SHEET_DATA) public data: any,
    private $authentication: AuthenticationService,
    private $department: DepartmentService,
    private $snackBar: MatSnackBar,
    private $bottomSheetRef: MatBottomSheetRef<DepartmentPlayComponent>,
    public config: ConfigService,
  ) {
    if (data.department == null) {
      this.action = 'Add';

      this.delfg = new FormGroup({
      });
    } else {
      this.action = 'Update';

      this.delfg = new FormGroup({
        name: new FormControl(
          '',
          [
            Validators.required,
            Validators.pattern(data.department.name),
          ],
        ),
      });

      this.depfg.patchValue({
        ...this.data.department,
      });
    }

    // ACL
    const role = this.$authentication.user?.role;

    if (role) {
      if (
        (this.config.role.rp == role.id) == false
      ) {
        this.depfg.disable();
        this.delfg.disable();
      }
    }
  }

  ngOnInit(): void {
  }

  play(event: any): void {
    event.preventDefault();

    if (this.depfg.valid) {
      this.sse = '';
      this.depfg.disable();
      switch (this.action) {
        case 'Add':
          this.$department.addDepartment(this.depfg.value).subscribe(
            () => {
              this.depfg.enable();
              this.$snackBar.open(`Department added`, '', {
                duration: 3000,
              });
              this.$bottomSheetRef.dismiss();
            },
            (error: HttpResponseModel) => {
              this.depfg.enable();
              this.sse = error.message;
            }
          );
          break;
        case 'Update':
          this.$department.updateDepartment(this.data.department.id, this.depfg.value).subscribe(
            () => {
              this.depfg.enable();
              this.$snackBar.open(`Department updated`, '', {
                duration: 3000,
              });
            },
            (error: HttpResponseModel) => {
              this.depfg.enable();
              this.sse = error.message;
            }
          );
          break;
      }
    }
  }

  delete(event: any): void {
    event.preventDefault();

    if (this.delfg.valid) {
      this.sse = '';
      this.delfg.disable();
      this.$department.deleteDepartment(this.data.department.id).subscribe(
        () => {
          this.delfg.enable();
          this.$snackBar.open(`Department and associated information deleted`, '', {
            duration: 3000,
          });
          this.$bottomSheetRef.dismiss();
        },
        (error: HttpResponseModel) => {
          this.delfg.enable();
          this.sse = error.message;
        }
      );
    }
  }

  dismiss(event: MouseEvent): void {
    this.$bottomSheetRef.dismiss();
    event.preventDefault();
  }
}
