import { AfterContentInit, Component, OnInit } from '@angular/core';
import { DepartmentModel } from '@models/department.model';
import { MatTableDataSource } from '@angular/material/table';
import { DepartmentService } from '@services/department.service';
import { BreakPointsService } from '@services/breakpoints.service';
import { MatBottomSheet } from '@angular/material/bottom-sheet';
import { DepartmentPlayComponent } from '@admin/org/department-play/department-play.component';
import { AuthenticationService } from '@services/authentication.service';
import { OrgModel } from '@models/org.model';
import { HttpResponseModel } from '@models/http/response.model';
import { MatSnackBar } from '@angular/material/snack-bar';
import { OrgService } from '@services/org.service';

@Component({
  selector: 'app-org',
  templateUrl: './org.component.html',
  styleUrls: ['./org.component.css']
})
export class OrgComponent implements OnInit, AfterContentInit {
  // Departments table
  dtdc: string[] = ['id', 'name', 'action'];
  dtds: MatTableDataSource<DepartmentModel>
    =
    new MatTableDataSource<DepartmentModel>([]);

  constructor(
    private $authentication: AuthenticationService,
    private $org: OrgService,
    private $department: DepartmentService,
    private $bottomSheet: MatBottomSheet,
    private $snackBar: MatSnackBar,
    public breakpoints: BreakPointsService,
  ) {
   }

  ngOnInit(): void {
  }

  ngAfterContentInit(): void {
    this.getDepartments();
  }

  getDepartments(): void {
    this.$department.getDepartments().subscribe(
      (departments: DepartmentModel[]) => {
        this.dtds.data = departments;
      },
    );
  }

  departmentPlay(department: DepartmentModel | null): void {
    this.$bottomSheet.open(DepartmentPlayComponent, {
      data: {
        department,
      }
    });
  }

  pickWallpaper(event: MouseEvent, wallpaper: any): void {
    event.preventDefault();

    wallpaper.click();
  }

  pickLogo(event: MouseEvent, logo: any): void {
    event.preventDefault();

    logo.click();
  }

  uploadLogo(event: any): void {
    event.preventDefault();

    if (event.target.files.length > 0) {
      const [file] = event.target.files;

      this.$org.uploadLogo(file).subscribe(
        (result: HttpResponseModel) => {
          this.$snackBar.open(result.message, '', {
            duration: 3000,
          });
        },
        (error: HttpResponseModel) => {
          this.$snackBar.open(error.message, '', {
            duration: 3000,
          });
        }
      );
    }
  }

  uploadWallpaper(event: any): void {
    event.preventDefault();

    if (event.target.files.length > 0) {
      const [file] = event.target.files;

      this.$org.uploadWallpaper(file).subscribe(
        (result: HttpResponseModel) => {
          this.$snackBar.open(result.message, '', {
            duration: 3000,
          });
        },
        (error: HttpResponseModel) => {
          this.$snackBar.open(error.message, '', {
            duration: 3000,
          });
        }
      );
    }
  }

  get org(): OrgModel | undefined {
    return this.$authentication.user?.org;
  }

  get wallpaperPath(): string {
    return this.$org.wallpaperPath;
  }

  get logoPath(): string {
    return this.$org.logoPath;
  }
}
